import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Constants} from '../../../app.constants';
import {Location} from '@angular/common';
import {Feedbacks, IResponse} from '../../../interfaces/interfaces';
import {FeedbacksService} from '../../../services/feedbacks.service';

@Component({
  selector: 'app-view-feedback',
  templateUrl: './view-feedback.component.html',
  styleUrls: ['./view-feedback.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [FeedbacksService]
})
export class ViewFeedbackComponent implements OnInit {
  feedback: Feedbacks = null;
  public responses: IResponse[] = [];
  public response: IResponse;
  public NewResponseForm: FormGroup;
  feedbacks: Feedbacks[] = [];
  errorMessage: any;
  loading: boolean;
  pageLoading: boolean;
  onSuccess: any;
  deleteMessageEnabled: boolean;
  nameBadge: string;
  commentNameBadge: string;
  commentInitials: string;

  constructor(private feedbacksService: FeedbacksService,
              private formBuilder: FormBuilder,
              private route: ActivatedRoute,
              private router: Router, private _location: Location) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params['id'];
    this.getFeedback(id);
    this.buildForm();
  }


  backClicked() {
    this._location.back();
  }

  getFeedback(id: number) {
    this.pageLoading = true;
    this.feedbacksService.getFeedbackDetail(id)
      .subscribe((feedback: Feedbacks) => {
          this.feedback = feedback;
          // console.log(feedback)
          for (let j = 0; j < this.feedback.responses.length; j++) {
            this.commentNameBadge = this.feedback.responses[j].responder ? this.feedback.responses[j].responder.full_name : 'Anonymous';
            const initials = this.commentNameBadge.match(/\b\w/g) || [];
            this.commentInitials = ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
            this.feedback.responses[j].commentInitials = this.commentInitials;
          }
          this.NewResponseForm.patchValue({
            feedback_id: this.feedback.id,
          });
          this.pageLoading = false;

        }, (err) => {
          this.errorMessage = Constants.handleError(err, this.router);
          this.pageLoading = false;
        }
      );
  }

  buildForm() {
    this.NewResponseForm = this.formBuilder.group({
      feedback_id: [''],
      content: ['', Validators.compose([Validators.required])],
    })
  }

  onSubmit({value, valid}: { value: IResponse, valid: boolean }) {
    this.loading = true;
    if (value) {
      // console.log(value)
      this.feedbacksService.createResponse(value)
        .subscribe((responses: IResponse) => {
            if (responses) {
              Constants.succeeded('You have successfully responded', 'Responded!');
              window.setTimeout(function () {
                location.reload();
              }, 2000)
            } else {
              this.loading = false;
            }
          }, (err) => {
            this.loading = false;
            Constants.handleError(err, this.router, true);
          }
        )
    }

  }
}
