import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Location} from '@angular/common';
import {Constants} from '../../../app.constants';
import {FeedbacksService} from '../../../services/feedbacks.service';
import {Feedbacks} from '../../../interfaces/interfaces';

@Component({
  selector: 'app-feedback-list',
  templateUrl: './feedback-list.component.html',
  styleUrls: ['./feedback-list.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FeedbackListComponent implements OnInit {

  feedbacks: Feedbacks[] = [];
  searchText = '';
  toggle = false;
  selectedAll: any;
  loading: boolean;
  errorMessage: any;

  current_page: number;
  data: Feedbacks[];
  from: number;
  last_page: number;
  next_page_url: string;
  path: string;
  per_page: number;
  prev_page_url: string;
  to: number;
  total: number;

  constructor(public feedbacksService: FeedbacksService,
              private route: ActivatedRoute, private _Location: Location,
              private router: Router) {
  }

  ngOnInit() {
    this.submitSearch();
  }

  submitSearch($perPage = 15) {
    this.loading = true;
    this.feedbacks = [];
    this.last_page = -1;
    this.getFeedback('', this.searchText);
  }

  getFeedback(url, params = '') {
    this.feedbacksService.getPageFeedbacks(url, params).subscribe((data) => {
      this.feedbacks = data.data;
      this.current_page = data.current_page;
      this.from = data.from;
      this.last_page = data.last_page;
      this.next_page_url = data.next_page_url;
      this.path = data.path;
      this.prev_page_url = data.prev_page_url;
      this.per_page = data.per_page;
      this.to = data.to;
      this.total = data.total;
      this.loading = false;
    }, (err) => {
      this.errorMessage = Constants.handleError(err, this.router);
      this.loading = false;
    })
  }

  deleteFeedback(feedback) {
    Constants.confirm({
        message: 'Are you sure you want to delete this feedback?',
        confirmText: 'Yes delete!'
      }, (willDelete) => {
        if (willDelete.value) {
          this.feedbacksService.deleteFeedback(feedback.id).subscribe((status: boolean) => {
            if (status) {
              Constants.succeeded('Feedback has been deleted!', 'Successful');
              this.feedbacks.splice(this.feedbacks.indexOf(feedback), 1);
            } else {
              this.errorMessage = 'Unable to delete Feedback'
            }
          }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.loading = false;
          });
        }
      }
    );
  }

}
