import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FeedbackListComponent} from './feedback-list/feedback-list.component';
import {LoaderModule} from '../../shared/loader/loader.module';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {ViewFeedbackComponent} from './view-feedback/view-feedback.component';

export const routes: Routes = [
  {path: '', redirectTo: '/app/feedbacks/list', pathMatch: 'full'},
  {path: 'list', component: FeedbackListComponent},
  {path: 'view/:id', component: ViewFeedbackComponent},
];

@NgModule({
  imports: [
    CommonModule,
    PerfectScrollbarModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule,
    CustomErrorModule,
    LoaderModule
  ],
  declarations: [FeedbackListComponent, ViewFeedbackComponent]
})
export class FeedbacksModule {
}
